:root{

	/* COLORS */
	--lemonGlacier: #F5FF00;
	--eerieBlack: #212529;

}

.searchBar{
	width: 20rem;
	background-color: var(--eerieBlack);
	padding: 1rem 1rem;
	display: flex;
}

.input{
	border-radius: .3rem;
	padding-left: .8rem;
	outline: none;
	border-color: var(--lemonGlacier);
	height: 2rem;
}

#btnBuscar{
	border: .1rem solid var(--lemonGlacier);
	color: var(--lemonGlacier);
	background-color: var(--eerieBlack);
	width: 100%;
	border-radius: .3rem;
	transition: all .3s ease-in;
	padding: 0 2rem .2rem ;
	margin-left: 1rem;
}

#btnBuscar:hover{
	color: white;
	border-color: white;
}


/* RESPONSIVE DESIGN */

/* TABLET VERTICAL | 768px */
@media (max-width: 991px) and (min-width: 768px){
	.searchBar{
		width: 50%;
	}
}

/* MOVIL | 375px */
@media (max-width: 767px){
	.searchBar{
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center
	}
	.input{
		width: 100%;
		margin: .5rem auto;
	}
	#btnBuscar{
		margin: 0;
		width: 100%;
	}
}

