.NavBar{
	border: .1rem solid black;
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: #212529;
	padding: 0 4rem;
}

.nombreEmpresa{
	font-size: 1.4rem;
	color: white;
	margin-right: 38rem;
}


/* RESPONSIVE DESIGN */

/* TABLET HORIZONTAL | 1024px */
@media (max-width: 1199px) and (min-width: 992px){

	.NavBar{
		display: flex;
	}

	.logotipo{
		margin: 1rem auto;
	}

	.nombreEmpresa{
		margin: 1rem 18rem 1rem 0;

	}

}

/* TABLET VERTICAL | 768px */
@media (max-width: 991px) and (min-width: 768px){

	.NavBar{
		display: flex;
		flex-direction: column;
	}

	.logotipo{
		margin: 1rem auto;
	}

	.nombreEmpresa{
		margin: 1rem auto;
	}

}

/* MOVIL | 375px */
@media (max-width: 767px){
	.NavBar{
		display: flex;
		flex-direction: column;
	}

	.logotipo{
		margin: 1rem auto;
	}

	.nombreEmpresa{
		margin: 1rem auto;
	}
	
}

