.card{
	margin: 2rem auto;
	display: flex;
	flex-direction: column;
	border:  .1rem solid #F5FF00;
	width: 18rem;
	height: auto;
	border-radius: .5rem;
	transition: all .2s ease;
	background-color: rgba(0, 0, 0, .35);
}

.card:hover{
	scale: 102%;
	box-shadow: 0 0 3rem -1rem;
}

.btnClose{
	background-color: #F5FF00;
	width: 1.5rem;
	height: 1.7rem;
	color: black;
	border-radius: .2rem;
	margin-right: .5rem;
	margin-top: .5rem;
	cursor: pointer;
	position: relative;
	left: 16rem;
}

.btnClose:hover{
	background-color: white;
}

.nameCity{
	font-weight: bold;
	font-size: 1.3rem;
	color: white;
	margin: 0;
}

.country{
	font-size: 2.8rem;
	color: white;
	margin: 0;
}

.description{
	color: white;
	font-weight: 300;
	font-size: 1.4rem;
}

.actualText{
	margin: 0;
}

.tempActual{
	font-size: 3rem;
	color: white;
}

.temperatures{
	display: flex;
	justify-content: space-around;
	align-items: center;
	color: white;
	padding-bottom: 1rem;
	font-size: 1.5rem;
}

.textMin, .textMax, .actualText{
	font-weight: bold;
	color: #F5FF00;
	font-size: 1.1rem;
}


